.notification-enter {
  visibility: hidden;
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}

.notification-enter.notification-enter-active {
  visibility: visible;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  -webkit-transition: all 0.4s;
  transition: all 0.4s;
}

.notification-leave {
  visibility: visible;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}

.notification-leave.notification-leave-active {
  visibility: hidden;
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
  -webkit-transition: all 0.4s;
  transition: all 0.4s;
}

.notification:before {
  position: absolute;
  top: 50%;
  left: 15px;
  margin-top: -14px;
  display: block;
  font-family: 'Notification';
  width: 28px;
  height: 28px;
  font-size: 28px;
  text-align: center;
  line-height: 28px;
}

.notification-info {
  background-color: #2f96b4;
}

.notification-info:before {
  content: "\F05A";
}

.notification-success {
  background-color: #51a351;
}

.notification-success:before {
  content: "\F058";
}

.notification-warning {
  background-color: #f89406;
}

.notification-warning:before {
  content: "\F06A";
}

.notification-error {
  background-color: #bd362f;
}

.notification-error:before {
  content: "\F057";
}

.react-dual-listbox {
  display: flex;
  align-items: center;
  box-sizing: border-box;
}

.react-dual-listbox * {
  box-sizing: border-box;
}

.react-dual-listbox input:disabled,
.react-dual-listbox select:disabled {
  background: #eee;
  cursor: not-allowed;
}

.react-dual-listbox button,
.react-dual-listbox select {
  line-height: 1.42857;
  font-family: inherit;
}

.rdl-list-box {
  display: flex;
  flex: 1 1;
  flex-direction: column;
  align-self: stretch;
}

.rdl-filter,
.rdl-control {
  display: block;
  border: 1px solid #ccc;
  border-radius: 2px;
  padding: 8px 12px;
  width: 100%;
  color: #333;
  font-size: 14px;
}

.rdl-filter {
  margin-bottom: 10px;
}

.rdl-control-container {
  display: flex;
  flex: 1 0 auto;
  flex-direction: column;
}

.rdl-control-label {
  padding: 2px 4px;
}

.rdl-control-label.rdl-sr-only {
  position: absolute;
  clip: rect(0 0 0 0);
}

.rdl-control {
  flex: 1 0 auto;
}

.rdl-control optgroup {
  font: inherit;
  font-weight: 700;
}

.rdl-actions {
  display: flex;
  flex: 0 0 auto;
  flex-direction: column;
  margin: 0 10px;
}

.rdl-has-header .rdl-actions {
  padding-top: 31px;
}

.rdl-has-filter .rdl-actions {
  padding-top: 44px;
}

.rdl-has-header.rdl-has-filter .rdl-actions {
  padding-top: 75px;
}

.rdl-actions-right,
.rdl-actions-left {
  display: flex;
  flex-direction: column;
}

.rdl-actions-right {
  margin-bottom: 10px;
}

.rdl-move {
  margin-bottom: 5px;
  border: 1px solid #ccc;
  border-radius: 2px;
  background: #fff;
  cursor: pointer;
  padding: 5px 10px;
  color: #333;
  font-size: 12px;
}

.rdl-move:active:not(:disabled), .rdl-move:focus:not(:disabled) {
  border-color: #8c8c8c;
  background: #e6e6e6;
}

.rdl-move:focus:not(:disabled) {
  outline: thin dotted;
  outline-offset: -2px;
}

.rdl-move:hover:not(:disabled) {
  border-color: #adadad;
  background: #e6e6e6;
}

.rdl-move:disabled {
  opacity: .5;
  cursor: not-allowed;
}

.rdl-move:last-child {
  margin-bottom: 0;
}

.rdl-move i {
  margin: 0 -1px;
}

.rdl-align-top .rdl-available {
  margin-right: 10px;
}

.rdl-align-top .rdl-selected {
  margin-left: 10px;
}

.rdl-align-top .rdl-control {
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}

.rdl-align-top .rdl-actions-left,
.rdl-align-top .rdl-actions-right {
  flex-direction: row;
  margin: 0;
}

.rdl-align-top .rdl-move {
  flex: 0 1 50%;
}

.rdl-align-top .rdl-move:first-child {
  margin-bottom: 0;
  border-right: 0;
  border-top-right-radius: 0;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}

.rdl-align-top .rdl-move:last-child {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}

